import { useState } from 'react';
import { Box, Button, Divider, TextField } from '@mui/material';
import MainCard from '../cards/MainCard';
export const SettingsPassword = (props) => {
  const [values, setValues] = useState({
    password: '',
    confirm: ''
  });

  const handleChange = (event) => {
    setValues({
      ...values,
      [event.target.name]: event.target.value
    });
  };

  return (
    <form {...props}>
      <MainCard
        title="Password"
      // subheader="Update password"

      >
        <TextField
          fullWidth
          label="Current Password"
          margin="normal"
          name="cÃ¥urrentPassword"
          onChange={handleChange}
          type="password"
          value={values.password}
          variant="outlined"
        />
        <TextField
          fullWidth
          label="Password"
          margin="normal"
          name="password"
          onChange={handleChange}
          type="password"
          value={values.password}
          variant="outlined"
        />
        <TextField
          fullWidth
          label="Confirm password"
          margin="normal"
          name="confirm"
          onChange={handleChange}
          type="password"
          value={values.confirm}
          variant="outlined"
        />
        <Divider />
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'flex-end',
            p: 2
          }}
        >
          <Button
            color="primary"
            variant="contained"
          >
            Update
          </Button>
        </Box>
      </MainCard>
    </form>
  );
};
